* {
  box-sizing: border-box;
}

a {
  color: royalblue;
  text-decoration-line: none;
}

a:visited {
  color: royalblue;
}

button {
  font-family: inherit; 
  font-size: inherit; 
  padding: 5px 10px;
  user-select: none;
}

button:focus {
  outline: solid 1px royalblue;
}

label {
  user-select: none;
}

textarea {
  border: 1px solid #888;
  border-radius: 5px;
}

textarea:focus {
  border: 1px solid royalblue;
  outline: none;
}

body, html {
  margin: 0; 
  padding: 0;
  font-family: "Open Sans";
  font-size: 16px; 
}

body {
  display: grid; 
  justify-content: center; 
}

#wrapper {
  max-width: 700px; 
  padding: 20px;
  margin-top: 50px;
  display: grid; 
  justify-items: left;
}

#description {
  margin: 0;
}

#instructions {
  color: #444;
  margin-top: 5px;
  margin-bottom: 25px;
  font-size: 0.95em;
  list-style-type: square; 
}

#source {
  font-size: smaller;
  color: #666;
}

#file-button {
  margin-right: 20px;
}

#file-input {
  opacity: 0; 
  width: 0; 
  height: 0; 
  position: fixed;
  left: 0; 
  top: 0;
}

#editor-wrapper {
  width: 100%; 
  margin-top: 15px;
}

#editor {
  font-size: inherit;
  font-family: inherit;
  padding: 10px;
  margin-top: 5px;
  width: 100%; 
  height: 500px;
}

#editor-buttons {
  margin-top: 5px;
  display: grid; 
  justify-items: left; 
  grid-template-columns: auto 1fr auto auto;
  grid-template-areas: "a b c d"; 
  grid-column-gap: 10px;
  font-size: 0.95em;
}

#editor-buttons > button:nth-child(0n+2){
  justify-self: right;
  grid-area: c;
}

#editor-buttons > button:nth-child(0n+3){
  justify-self: right;
  grid-area: d;
}

#history {
  margin: 50px 0;
}

#history > div:first-child {
  font-size: 1.2em;
}

#history > div:nth-child(0n+2) {
  font-size: 0.9em;
  color: #777;
}

#history > ul {
  list-style: none; 
  padding: 0;
  margin: 0;
  margin-top: 10px;
  display: grid; 
}

.history-name {
  color: royalblue;
}

.history-date {
  font-size: 0.8em;
}

.history-item {
  background-color: white;
  padding: 5px;
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.history-item:hover {
  background-color: #eee;
}

.history-item:active {
  background-color: #ddd;
}

.history-remove-btn {
  background: #ff4444;
  color: white;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  transition: all 0.2s ease;
}

.history-remove-btn:hover {
  background: #cc0000;
  transform: scale(1.1);
}

.history-remove-btn:active {
  transform: scale(0.95);
}

.history-item-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

#cover {
  background-color: rgba(0, 0, 0, 0.219); 
  position: fixed;
  left: 0; 
  top: 0;
  width: 100vw; 
  height: 100vh;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":";AACA;EACE,sBAAsB;AACxB;;AAEA;EACE,gBAAgB;EAChB,0BAA0B;AAC5B;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,oBAAoB;EACpB,kBAAkB;EAClB,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,2BAA2B;EAC3B,aAAa;AACf;;AAEA;EACE,SAAS;EACT,UAAU;EACV,wBAAwB;EACxB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,gBAAgB;EAChB,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,WAAW;EACX,eAAe;EACf,mBAAmB;EACnB,iBAAiB;EACjB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,WAAW;AACb;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,UAAU;EACV,QAAQ;EACR,SAAS;EACT,eAAe;EACf,OAAO;EACP,MAAM;AACR;;AAEA;EACE,WAAW;EACX,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,oBAAoB;EACpB,aAAa;EACb,eAAe;EACf,WAAW;EACX,aAAa;AACf;;AAEA;EACE,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,yCAAyC;EACzC,8BAA8B;EAC9B,qBAAqB;EACrB,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,WAAW;AACb;;AAEA;EACE,gBAAgB;EAChB,UAAU;EACV,SAAS;EACT,gBAAgB;EAChB,aAAa;AACf;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,uBAAuB;EACvB,YAAY;EACZ,iBAAiB;EACjB,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,mBAAmB;EACnB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,eAAe;EACf,iBAAiB;EACjB,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iBAAiB;EACjB,yBAAyB;AAC3B;;AAEA;EACE,mBAAmB;EACnB,qBAAqB;AACvB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,OAAO;EACP,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,sCAAsC;EACtC,eAAe;EACf,OAAO;EACP,MAAM;EACN,YAAY;EACZ,aAAa;AACf","sourcesContent":["\n* {\n  box-sizing: border-box;\n}\n\na {\n  color: royalblue;\n  text-decoration-line: none;\n}\n\na:visited {\n  color: royalblue;\n}\n\nbutton {\n  font-family: inherit; \n  font-size: inherit; \n  padding: 5px 10px;\n  user-select: none;\n}\n\nbutton:focus {\n  outline: solid 1px 